
.about-wrapper {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    padding-top: 0;

    & .about-item {
        flex: 1;
        display: flex;
        justify-content: space-evenly;
        padding: 4vw;
        gap: 4vw;
    }
    
     & .about-item .content {
        flex: 1.5;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        & h2 {
            font-family: var(--font-extraBold);
            font-size: var(--font-size-xl);
            color: var(--teal-dark-color);
        }

        & p {
            font-family: var(--font-regular);
            font-size: var(--font-size-m);
            color: var(--grey-dark-color);
            text-align: center;

            & span {
                font-family: var(--font-bold);
                text-decoration: underline;
            }
        }
    }
    
    & .about-item .image {
        flex: 1.2;
        display: flex;
        justify-content: center;
        margin: 1vw;
        
        & img {
            width: 100%;
            object-fit: contain;
            opacity: 100%;
        }
    }

    & #core-values {
        flex: 1;
        flex-direction: column;
        background-color: var(--grey-light-color);        

        & .content p {
            margin: 4vw;
        }
    }


    & .about-item .team {
        flex: 2.0;
        display: flex;
        justify-content: space-around;


        & .team-member {
            display: flex;
            flex-direction: column;
            justify-content: flex-start;
            align-content: center;
            text-align: center;

            & img { 
                flex: 2;
                width: 20vw;
                height: 20vw;
                border-radius: 20%;
                align-self: center;
            }

            & h3 {
                flex: 1;
            }
            & p {
                flex: 1;
                font-size: var(--font-size-s);
            }
        }
    }
}

@media (max-width: 768px) {
    .about-item {
        flex-direction: column;
        margin-left: 3vw;
        margin-right: 3vw;
    }

    .about-item .image {
        width: 80%;
        align-self: center;
    }

    #core-values {
        margin: 0;
    }
    
}
